const initialState = {
	weather: {
		city: 'Taipei City',
		date: "Wed, 26 Oct 2016 08:00 AM CST",
		temp: "80",
		text: "Mostly Sunny"
	}
}

const weatherReducer = (state = initialState, action) => {
	switch (action.type) {
		case 'WEATHER_SHOW_WEATHER':
			const condition = action.query.results.channel.item.condition
			state = condition
			return state
		default:
			return state
	}
}

export default weatherReducer


